@charset "UTF-8";
@use "sass:math";
@import "mixins";
@import "core";

$board-pos-top:170px;//360px;
$bg-color: #d50b1c;
$bg-grandient: linear-gradient(180deg, #d50b1c 0%, #e11e50 25%, #dd3185 50%, #973c9d 75%, #6e42ab 100%);

body{
    background: $bg-color;
    background: $bg-grandient;
}

